.header {
  background: #f7f7f7;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 15px;

  .dark-theme & {
    background: #252627;
  }

  &__right {
    display: flex;
    flex-direction: row;
    align-items: center;

    @media #{$media-size-phone} {
      flex-direction: row-reverse;
    }
  }

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 auto;
    width: 760px;
    max-width: 100%;
  }
}

.theme-toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
  cursor: pointer;
}

.menu-trigger {
  &:hover {
    color: rgb(131, 131, 131);
    .dark-theme & {
      color: rgb(218, 218, 218);
    } 
  }
}

.theme-toggler {
  fill: currentColor;

  &:hover {
    color: rgb(131, 131, 131);
    .dark-theme & {
      color: rgb(218, 218, 218);
    } 
  }
}

.unselectable {
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}
